<template>
    <div class="max-w-md m-auto">
        <div @focusin="selected = true" class="bg-white w-full rounded-md relative shadow">
            <input
                @keypress.enter="onSearch"
                v-model="searched"
                class="h-14 w-full text-xl outline-none border-none focus:rounded-b-none rounded-md pr-16 pl-5 shadow-sm"
                type="text"
                :placeholder="placeholder"
            />
            <div
                v-show="selected && searched !== ''"
                class="bg-white w-full py-3 px-2 max-h-72 overflow-y-auto absolute z-10 rounded-b-md shadow"
            >
                <p
                    @click="
                        () => {
                            this.searched = s;
                            this.selected = false;
                            this.onSearch();
                        }
                    "
                    v-show="searched !== '' && s.toLowerCase().includes(searched.toLowerCase())"
                    v-for="s in suggestions"
                    :key="s"
                    class="py-2 px-3 w-full text-xl cursor-default rounded-md hover:bg-gray-300"
                >
                    {{ s }}
                </p>
            </div>
            <div class="absolute right-0 top-3 h-14 w-14 text-center text-blue-600 cursor-pointer text-2xl">
                <i @click="onSearch" class="fas fa-search"></i>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "SearchBox",
    props: {
        suggestions: Array,
        placeholder: String,
    },
    data() {
        return {
            selected: false,
            searched: "",
        };
    },
    mounted() {
        document.addEventListener("click", this.onClickOut);
    },
    unmounted() {
        document.removeEventListener("click", this.onClickOut);
    },
    methods: {
        onClickOut(event) {
            if (!this.$el.contains(event.target)) this.selected = false;
        },
        onSearch() {
            this.$emit("search", this.searched);
            this.searched = "";
        },
    },
};
</script>
