<template>
    <SearchBox @search="handleSearch" :suggestions="provs" placeholder="Inserisci una provincia..." />

    <div class="mt-8 text-center">
        <h1 class="text-4xl font-bold mb-8">{{ prov }}</h1>
        <button
            v-if="data"
            @click="this.$router.push({ name: 'Graph', params: { type: 'province', target: this.prov } })"
            class="rounded block m-auto mb-8 bg-blue-600 p-4 text-3xl text-white focus:outline-none focus:border-transparent transform active:scale-95"
        >
            Vai al Grafico
        </button>
        <h1 v-if="data" class="m-8 text-2xl font-bold">
            Dati del
            {{ new Date(data.data).toLocaleString("default", { day: "numeric", month: "long", year: "numeric" }) }}
        </h1>
        <DataCard v-if="data" title="Totale Casi" :data="data.totale_casi" />
    </div>
</template>

<script>
import SearchBox from "@/components/SearchBox";
import DataCard from "@/components/DataCard";

export default {
    name: "Province",
    props: {
        province: String,
    },
    components: {
        SearchBox,
        DataCard,
    },
    data() {
        return {
            provs: [],
            data: null,
            prov: "Nessuna Provincia Selezionata",
        };
    },
    async created() {
        const req = await fetch("api/province");
        this.provs = await req.json();
        if (this.province) this.handleSearch(this.province);
    },
    methods: {
        async handleSearch(value) {
            const req = await fetch(`api/province/latest/${value}`);
            this.data = await req.json();
            this.prov = this.data ? value : "Nessuna Provincia Selezionata";
        },
    },
};
</script>
